<template id="profile">
    <v-container>
        <div><h2>Hello, {{emailFromLogin}}</h2></div>
    </v-container>
</template>
<script>
    import { userBus } from '../main';
    export default {
        name: 'User',
        data() {
            return {
                emailFromLogin: this.$root.email,
            }
        },
        created() {
            userBus.$on('sendemail',(email) => {
                this.$root.email = email;
            });
        },
    }
</script>
