import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/MainFrame.vue'
import User from './views/User.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
      {
        path: '/',
        component: Home
      },
      {
        path: '/user',
        component: User
      },
  ]
})
